.boxnav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  border: 0;
  left: auto;
  right: 0;
  width: 47px;
  height: 47px;
  border-radius: 50%;
  flex: none;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
}

.boxnav__item:focus {
  outline: none;
}

.boxnav__item svg {
  display: block;
}

.boxnav__item--prev:hover,
.boxnav__item--next:hover {
  color: #858585;
}

.boxnav__label {
  padding: 0 0.5rem;
  font-size: 1.15rem;
  position: relative;
}

.boxnav__label--total::before {
  content: '';
  position: absolute;
  right: 100%;
  top: 0;
  width: 2px;
  height: 1.45rem;
  background: #000;
  transform: rotate(22.5deg);
}

.icon--caret-rot {
  transform: rotate(180deg);
}

.gallery__wrapper h2 {
  margin-bottom: 60px;
}

.gallery__item {
  position: relative;
}

.page-contacts {
  height: 100vh;
  background: #292B33;
  overflow: hidden;
  color: white;
}

.page-contacts h2 {
  color: white;
}

.page-contacts__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 7vw;
  height: 100%;
  width: 100%;
}

.page-contacts__wrapper p {
  width: 90%;
  margin-top: 20px;
}

.page-contacts__wrapper .main-footer__content__left-content__contacts-items.with-margin {
  margin-top: 0px;
}

.page-contacts-wrapper__right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 50%;
  padding-left: 7vw;
  padding-top: 20vh;
  padding-bottom: 20vh;
}

.page-contacts-wrapper__left {
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.page-contacts-wrapper__left img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.header__photo {
  height: 100vh;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

.bigger {
  width: 41%;
}

.history-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.history-wrapper__left {
  width: 50%;
  height: 35vw;
}

.history-wrapper__left img {
  max-width: 100%;
  display: block;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.history-wrapper__right {
  width: 50%;
  margin-left: 70px;
}

.history-wrapper__right p {
  margin-top: 60px;
}

.custom-width {
  width: 70%;
}

.review-header {
  width: 100%;
  position: relative;
}

.review-header__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 13vw;
  padding-right: 13vw;
  position: relative;
  padding-top: 160px;
}

.reviews-content-wrapper {
  opacity: 0;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.reviews-content-wrapper__left {
  width: 40%;
}

.reviews-content-wrapper__left .TA_cdswritereviewnew {
  margin-top: 58px;
}

.reviews-content-wrapper__left .sec-p {
  margin-top: 60px;
}

.reviews-content-wrapper__left .TA_cdsratingsonlynarrow {
  margin-top: 110px;
}

.reviews-content-wrapper__left h2 {
  margin-top: 38px;
}

.reviews-content-wrapper__right {
  width: 40%;
}

.reviews-item {
  padding-top: 40px;
  padding-bottom: 60px;
  border-bottom: 1px rgba(0, 0, 0, 0.1) solid;
}

.reviews-interface {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  line-height: 1em;
}

.reviews-name {
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.7);
}

.reviews-date {
  font-size: 14px;
  color: rbga(#54555C, 0.7);
}

.reviews-rate {
  display: flex;
  margin-top: 12px;
  width: fit-content;
}

.reviews-rate.assessment {
  margin-top: 0px;
  margin-left: 65px;
}

.reviews-rate.assessment svg {
  display: block;
  margin-right: 8px;
  width: 18px;
  height: 18px;
  fill: #54555C;
}

.reviews-rate svg {
  display: block;
  margin-right: 8px;
  width: 12px;
  fill: #54555C;
  transition: fill .2s ease;
}

.reviews-rate svg.active {
  fill: #19A8D5;
}

.reviews-rate svg.disabled {
  pointer-events: none;
}

.reviews-review {
  margin-top: 20px;
}

.reviews-rating {
  display: flex;
  align-items: baseline;
  margin-top: 60px;
  position: relative;
  font-family: 'GT Medium', serif;
  font-weight: 500;
  font-size: 16px;
}

.reviews-rating .review-label {
  opacity: 0.7;
  line-height: 1em;
}

.reviews-items {
  display: flex;
  flex-direction: column-reverse;
}

.review-header-desc {
  font-size: 24px;
  margin-top: 20px;
  opacity: 0;
  transform: translateX(40px);
}

.button.review {
  margin-top: 25px;
}

.mob {
  display: none;
}

